nav{
    height: 4rem;
    width: 100%;
    background-color: #ffffff;
    z-index: 10;
    border-bottom: 1px solid #f1f1f1;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    display: flex;
}

.nav_container{
    display: flex;
    width: 80%;
    margin: auto;
}

.logo h2{
    font-size: 20px;
    text-align: center;
    display: inline;
}

.logo a{
    text-decoration: none;
    color: #5B33FF;
}


.mobile_navigation{
    display: none;
}

@media (max-width: 768px){
    .desktop_navigation{
        display: none;
    }

    .mobile_navigation{
        display: flex;
    }

    .container {
        display: inline-block;
        cursor: pointer;
      }
      
      .bar1, .bar2, .bar3 {
        width: 35px;
        height: 2px;
        background-color: rgb(165, 165, 165);
        margin: 6px 0;
        transition: 0.4s;
      }

      .change .bar1 {
        -webkit-transform: rotate(-45deg) translate(-9px, 3px);
        transform: rotate(-45deg) translate(-9px, 3px);
      }
      
      .change .bar2 {opacity: 0;}
      
      .change .bar3 {
        -webkit-transform: rotate(45deg) translate(-8px, -4px);
        transform: rotate(45deg) translate(-8px, -4px);
      }

      .hamburger{
          position: absolute;
          top: 20%;
          right: 5%;
          cursor: pointer;
      }

      .nav-links{
          display: flex;
          flex-direction: column;
          position: absolute;
          top: 4rem;
          right: 0;
          width: 100% !important;
          background: white;
          border-top: 2px solid #5B33FF;
      }

      .nav-links li{
          margin: 1rem 0;
      }

      .logo{
          width: 50% !important;
      }

      .nav_container{
          width: 100% !important;
      }

     
}





